.loginBody {
  background-image: url("../../img/fondoLogin.png");
  background-color: black;
  min-height: 100vh;
  background-size: cover;
  /* background-position: center; */
  display: flex;
  align-items: center;
  justify-content: center;
}
.registroBody {
  background-image: url("../../img/fondoRegistro.png");
  background-color: black;
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loginCard {
  background: rgba(255, 255, 255, 0.25);
  width: 35vw;

  min-height: 80vh;
margin-top: 5%;
  color: white;
  border: 0px;
  border-top: 1.5px solid rgba(255, 255, 255, 0.9);
  border-left: 1.5px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.h3Login {
  font-family: var(--nerko);
  font-weight: 400;
  font-size: 3rem;
  text-transform: uppercase;
  margin-bottom: 1.5%;
}
.loginForm {
  display: flex;
  flex-direction: column;
  margin-top: 0;
}
.loginForm2 {
    display: flex;
    flex-direction: column;
    margin-top: 0;
    padding: 2%;
  }
.labelLogin {
  font-family: var(--nerko);
  font-weight: 400;
  font-size: 1.8rem;
  text-align: left;
}
.inputIniciar {
  width: 25vw;
  height: 3vh;
  padding: 3%;
  margin-bottom: 4%;

  background: rgba(255, 255, 255, 0.25);
  font-family: var(--nerko);
  font-size: 1.2rem;

  color: white;
  border-radius: 10px;
  border: 0px;
  border-top: 1.5px solid rgba(255, 255, 255, 0.9);
  border-left: 1.5px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.5);
}
.chiquitin {
  width: 42%;
}
.nombreRegistro {
  width: 100%;
  height: 3vh;
  padding: 3%;
  margin-bottom: 4%;

  background: rgba(255, 255, 255, 0.25);
  font-family: var(--nerko);
  font-size: 1.2rem;

  color: white;
  border-radius: 10px;
  border: 0px;
  border-top: 1.5px solid rgba(255, 255, 255, 0.9);
  border-left: 1.5px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.5);
}
.enviarLogin {
  background: rgba(255, 255, 255, 0.25);
  border-radius: 10px;
  border: 0px;
  border-top: 1.5px solid rgba(255, 255, 255, 0.9);
  border-left: 1.5px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.5);
  align-self: center;
  width: 27vw;
  height: 5.5vh;
  margin-top: 4%;
  font-family: var(--nerko);
  color: white;
  transition: 0.2s;
  font-size: 1.5rem;
}
.enviarLogin:hover {
  background: rgba(255, 255, 255, 0.45);
  cursor: pointer;
}
.enviarLoginErr {
  background: rgba(255, 0, 0, 0.603);
  border-radius: 10px;
  border: 0px;
  border-top: 1.5px solid rgba(255, 0, 0, 0.9);
  border-left: 1.5px solid rgba(255, 0, 0, 0.5);
  border-right: 1px solid rgba(255, 0, 0, 0.5);
  align-self: center;
  width: 27vw;
  height: 5.5vh;
  margin-top: 4%;
  font-family: var(--nerko);
  color: white;
  font-size: 1.5rem;
  transition: 0.2s;
}
.enviarLoginErr:hover {
  background: rgba(255, 0, 0, 0.703);
  cursor: pointer;
}
.h6Login {
  align-self: center;
  font-size: 1.8rem;
  font-family: var(--nerko);
  font-weight: 400;
  text-transform: uppercase;
  margin: 4% 0;
}
.errorH3 {
  font-family: var(--nerko);
  color: rgb(241, 27, 27);
  font-weight: 400;
  font-size: 1.2rem;
  text-align: center;
}

.googleLogin {
  align-self: center;
}
.googleLogin {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  border: 1px solid white;
  padding: 2%;
  color: rgba(89, 89, 89, 1);
  width: 70%;
  font-size: 1rem;
  font-family: var(--open);
}
.google-logo {
  height: 5.5vh;
  margin-right: 2%;
}
.chiquitinRegistro {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.chiquitinFlex1 {
    width: 48%;
    /* margin-left: 4%; */
}
.chiquitinFlex2 {
    width: 48%;
    /* margin-right: 6%; */
}
