  }
  .edadRegistro {
   text-align: center;
    height: 100%;
    padding: 3%;
    margin-bottom: 4%;
  
    background: rgba(255, 255, 255, 0.25);
    font-family: var(--nerko);
    font-size: 1.2rem;
  
    color: white;
    border-radius: 10px;
    border: 0px;
    border-top: 1.5px solid rgba(255, 255, 255, 0.9);
